#header{
    width: 100%;
    border-bottom: 2px solid #fff;
    position: fixed;
    background-color: transparent;
    z-index: 999;
}
.header_wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
}
.header_wrap>h1{
    height: 24px; width: 125px;
    line-height: 24px;
}
.header_wrap>h1>img{
    width: 100%;
    height: 100%;
}
.gnb{
    display: flex;
}
.gnb>li{
    padding-left: 30px;
}
.gnb>li>a{
    color: #fff;
    font-weight: bold;
}
